    renderContent(h, {root, node, data}) {
        return h('div', {
                attrs: {
                    title: data.name,
                },
                style: {
                    display: 'inline-flex',
                    alignItems: 'center',
                    width: 'calc(100% - 20px)',
                    height: '36px',
                    overflow: 'hidden',
                },
                class: {
                    a: data.id,
                    treeActive: this.curProObj.id === data.id || this.rdId === data.id && this.curProObj.id === '',
                },
                on: {
                    click: (event) => {
                        if (data.level === 2) {
                            this.curProObj = data;
                            this.communityShow = false;
                            this.rdId = data.rdId;

                            this.getProjectById()
                            setTimeout(() => {
                                this.setPagesize();
                                this.getStandard()
                            }, 200)

                        } else if (data.level === 1) {
                            this.rdId = data.id;
                            this.curProObj = {
                                id: '',
                            }
                            this.communityShow = true;
                            //this.getProjectList(data.id, data)
                            // data.expand = !data.expand;
                        }
                    }
                }
            },
            [
                h('i', {
                    class: {
                        iconfont: true,
                        iconxiaoqu: data.level === 1,
                        iconshoufei: data.level === 2,
                    },
                    style: {
                        lineHeight: `1`,
                        marginRight: '8px',
                        marginLeft: '8px',
                        cursor: `pointer`,
                    },
                }),

                h('i', {
                    style: {
                        fontStyle: 'normal',
